//------------------------------------------------------------------------------
// @object: Icon
//------------------------------------------------------------------------------
// @description
//
//------------------------------------------------------------------------------

//
// @settings
$icon-size: rem(24px) !default;
$icon-spacing: unit(1) !default;

//
// @scss

//
// Most commonly we want specific sizing and alignment for icons in links
// as such we target tthe most common approach to including icons svgs.
// However a modifier is available if you are usinf icon fonts
//

.icon {
    vertical-align: middle;
    width: $icon-size;
    height: $icon-size;
    margin-right: $icon-spacing;
}

.icon-font {
    @include typo-icon($icon-size);
    vertical-align: baseline;
}
